import { Component, OnInit, Input, ViewChild, NgModule, VERSION, AfterViewInit } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser'
import { Chart } from 'chart.js';

@Component({
  selector: 'chart-canvas',
  templateUrl: './canvas.component.html',
  styleUrls: ['./canvas.component.scss'],
})
export class CanvasComponent implements OnInit {

  @Input() data;
  @Input() title;
  @ViewChild('canvas') canvas;

  chart: any

  constructor() {

  }

  ngOnInit() {

    this.createBarChart(this.data);

  }

  createBarChart(dataInput: any) {

    let usageChart = {scales: {
      xAxes: [{
        barPercentage: 0.9,
        gridLines: {
          offsetGridLines: true
        },
        stacked: true
      }],
      yAxes: [{
        ticks: {
          beginAtZero: true
        },
        stacked: true
      }]
    }};

    let impressionChart = {scales: {
      xAxes: [{
        barPercentage: 0.9,
        gridLines: {
          offsetGridLines: true
        },
        stacked: true
      }],
      yAxes: [{
        ticks: {
          beginAtZero: true
        },
        stacked: true
      }]
    },
    tooltips: {
      mode: 'label',
      callbacks: {
        afterTitle: function () {
          window.total = 0;
        },
        label: function (tooltipItem, data) {
          var corporation = data.datasets[tooltipItem.datasetIndex].label;
          var valor = data.datasets[tooltipItem.datasetIndex].data[tooltipItem.index];
          window.total += valor;
          return corporation + ": " + valor.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
        },
        footer: function () {
          return "TOTAL: " + window.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
        }
      }
    }};

    let ctx = this.canvas.nativeElement
    ctx.height = 400;
    this.chart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: JSON.parse(dataInput["data"]).labels,
        datasets: JSON.parse(dataInput["data"]).datasets
      },
      options: this.title === 'impression' ? impressionChart : usageChart
      // {
        // plugins: {
        //   stacked100: { enable: true }
        // },
        //   scales: {
        //     yAxes: [{
        //         ticks: {
        //             min: 0,
        //             max: 100,
        //             callback: function(value) {
        //                 return value + "%"
        //             }
        //         },
        //         stacked: true,
        //         scaleLabel: {
        //             display: true,
        //             labelString: "Percentage"
        //         }
        //     }],
        //     xAxes: [{
        //       stacked: true
        //     }]
        //  }
      // }
    });


  }

}
