import { Component, OnInit, Input, ViewChild, NgModule, VERSION, AfterViewInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-select-machine',
  templateUrl: './select-machine.component.html',
  styleUrls: ['./select-machine.component.scss'],
})
export class SelectMachineComponent implements OnInit {

  modalTitle:string;
  // modelId:number;

  isSelectMachine:boolean = false;
  isIndeterminate:boolean;
  masterCheck:boolean;
  checkBoxList:any;

  constructor(private modalController: ModalController, private navParams: NavParams) {

  }

  ngOnInit() {
    this.checkBoxList = this.navParams.data.checkBoxList;
    this.modalTitle = this.navParams.data.paramTitle;
    this.checkEvent();
  }

  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalController.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalController.dismiss(this.checkBoxList);
  }

  checkMaster() {
    setTimeout(()=>{
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }
 
  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

}
